import { dynamicWrapper, createRoute } from '@/utils/core';

const routesConfig = app => ({
  path: '/machineRoom',
  title: '机房巡检',
  component: dynamicWrapper(app, [import('./model')], () => import('./components'))
});

export default app => createRoute(app, routesConfig);



